@mixin box-column($columns) {

  .box-item {
    flex-basis: 0;
    flex-grow:  1;
    align-self: stretch;
  }

  @for $i from 1 through $columns {
    .box-item-#{$i} {
      flex:       0 0 percentage($i / $columns);
      // è¦çç¶åç´  align-items å±æ§ï¼ä¸ç¨æå¿ç¶åç´ è®¾ç½®äºå¶ä»å¼
      // ææ»¡æ´ä¸ªåºå
      align-self: stretch;
    }
  }
}



